import * as React from 'react';
import { styled } from '@mui/material/styles';
import ArrowForwardIosSharpIcon from '@mui/icons-material/ArrowForwardIosSharp';
import MuiAccordion from '@mui/material/Accordion';
import MuiAccordionSummary from '@mui/material/AccordionSummary';
import MuiAccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';



const Accordion = styled((props) => (
    <MuiAccordion disableGutters elevation={0} square {...props} />
))(({ theme }) => ({
    border: `1px solid ${theme.palette.divider}`,
    '&:not(:last-child)': {
        borderBottom: 0,
    },
    '&:before': {
        display: 'none',
    },
}));

const AccordionSummary = styled((props) => (
    <MuiAccordionSummary
        expandIcon={<ArrowForwardIosSharpIcon sx={{ fontSize: '0.9rem' }} />}
        {...props}
    />
))(({ theme }) => ({
    backgroundColor:
        theme.palette.mode === 'dark'
            ? 'rgba(255, 255, 255, .05)'
            : 'rgba(0, 0, 0, .03)',
    flexDirection: 'row-reverse',
    '& .MuiAccordionSummary-expandIconWrapper.Mui-expanded': {
        transform: 'rotate(90deg)',
    },
    '& .MuiAccordionSummary-content': {
        marginLeft: theme.spacing(1),
    },
}));

const AccordionDetails = styled(MuiAccordionDetails)(({ theme }) => ({
    padding: theme.spacing(2),
    borderTop: '1px solid rgba(0, 0, 0, .125)',
}));

const textGraduacao = 12;

export default function CustomizedAccordions(props) {
    const [expanded, setExpanded] = React.useState('');

    let resumeData = props.resumeData;

    const handleChange = (panel) => (event, newExpanded) => {
        setExpanded(newExpanded ? panel : false);
    };

    return (
        <section id="graduacao">
            <Typography variant="h4" style={{ textAlign: 'center' }} id="titleCapoeira">
                Sistema de Graduação e Requisitos
            </Typography>
            <Accordion expanded={expanded === 'panel1'} onChange={handleChange('panel1')}>
                <AccordionSummary aria-controls="panel1d-content" id="panel1d-header">
                    <Typography variant='h5'>Sistema de Graduação para Crianças/Jovens</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    {
                        resumeData.degreesChildren.map((criancas) => {
                            return (
                                <Accordion expanded={expanded === 'panel1'} onChange={handleChange('panel1')}>
                                    <AccordionSummary aria-controls="panel1d-content" id="panel1d-header">
                                        <Typography variant='h3' style={{ padding: 12 }} >{criancas.nome}</Typography>

                                    </AccordionSummary>
                                    <AccordionDetails>
                                        <AccordionDetails>
                                            <Box sx={{ flexGrow: 1 }}>
                                                <Grid container spacing={2}>

                                                    <Grid item xs={2}>
                                                        <img src={criancas.image} alt={criancas.nome} width={100} />
                                                    </Grid>
                                                    <Grid item xs={10}>
                                                        {
                                                            criancas.requisitos.map((item) => {
                                                                return (
                                                                    <Typography fontSize={textGraduacao}>{item}</Typography>
                                                                )
                                                            })
                                                        }
                                                    </Grid>
                                                </Grid>
                                            </Box>

                                        </AccordionDetails>

                                    </AccordionDetails>
                                </Accordion>
                            )
                        })
                    }
                </AccordionDetails>
            </Accordion>
            <Accordion expanded={expanded === 'panel2'} onChange={handleChange('panel2')}>
                <AccordionSummary aria-controls="panel2d-content" id="panel2d-header">
                    <Typography variant='h5'>Sistema de Graduação para Adultos</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    {
                        resumeData.degrees.map((criancas) => {
                            return (
                                <Accordion expanded={expanded === 'panel2'} onChange={handleChange('panel2')}>
                                    <AccordionSummary aria-controls="panel1d-content" id="panel1d-header">
                                        <Typography variant='h3' style={{ padding: 12 }} >{criancas.nome}</Typography>


                                    </AccordionSummary>
                                    <AccordionDetails>
                                        <AccordionDetails>
                                            <Box sx={{ flexGrow: 1 }}>
                                                <Grid container spacing={2}>

                                                    <Grid item xs={2}>
                                                        <img src={criancas.image} alt={criancas.nome} width={100} />
                                                    </Grid>
                                                    <Grid item xs={10}>
                                                        {
                                                            criancas.requisitos.map((item) => {
                                                                return (
                                                                    <Typography fontSize={textGraduacao}>{item}</Typography>
                                                                )
                                                            })
                                                        }
                                                    </Grid>
                                                </Grid>
                                            </Box>

                                        </AccordionDetails>

                                    </AccordionDetails>
                                </Accordion>
                            )
                        })
                    }
                </AccordionDetails>
            </Accordion>

        </section>
    );
}